
import { computed, defineComponent } from "vue";
import { useLayoutStore } from "@/layouts/hooks";
import { nextTick, ref } from "vue";
export default defineComponent({
  name: "Dialog",
  props: {
    title: {
      type: String,
      default: "",
    },
    closeOnClickModal: {
      type: Boolean,
      default: false,
    },
    showCancel: {
      type: Boolean,
      default: true,
    },
  },
  setup(props, { expose }) {
    const innerTitle = computed(() => props.title || "");
    const dialogRef = ref();
    const dialogVisible = ref(false);
    const loading = ref(false);
    const store = useLayoutStore();
    const _callback = ref();
    const isMobileScreen = computed(() => {
      return store.state.device === "mobile";
    });
    function show(callback: () => void) {
      dialogVisible.value = true;
      loading.value = false;
      nextTick(() => {
        const contentElement = document.querySelector(
          ".dialog__content-wrapper"
        );
        contentElement?.scrollTo({ top: 0 });
      });
      _callback.value = callback;
    }
    function close() {
      dialogVisible.value = false;
      loading.value = false;
    }
    function onConfirm() {
      if (_callback.value) {
        _callback.value(dialogRef.value);
      } else {
        close();
      }
    }
    function showLoading() {
      loading.value = true;
    }
    function closeLoading() {
      loading.value = false;
    }
    expose({
      show,
      close,
      showLoading,
      closeLoading,
    });
    return {
      dialogRef,
      dialogVisible,
      loading,
      isMobileScreen,
      innerTitle,
      onConfirm,
      close,
      show,
      showLoading,
      closeLoading,
    };
  },
});
