
import { Refresh as RefreshIcon } from "@element-plus/icons";
import { computed, defineComponent, PropType, reactive } from "vue";

export default defineComponent({
  name: "TableFooter",
  emits: ["pageChanged", "refresh"],
  props: {
    pageSizes: {
      type: Array as PropType<Array<number>>,
      default: () => {
        return [10, 20, 30, 40];
      },
    },
    showRefresh: {
      type: Boolean,
      default: true,
    },
    position: {
      type: String as PropType<"left" | "center" | "right">,
      default: "center",
    },
  },
  setup(props, { emit }) {
    const pageModel = reactive({
      currentPage: 1,
      pageSize: 10,
      totalSize: 0,
    });
    const pageSizeChanged = (pageSize: number) => {
      pageModel.pageSize = pageSize;
      pageModel.currentPage = 1;
      emit("pageChanged", pageModel);
    };
    const currentChanged = (currentPage: number) => {
      pageModel.currentPage = currentPage;
      emit("pageChanged", pageModel);
    };
    const withPageInfoData = (otherParams = {}) => {
      return {
        ...otherParams,
        page: pageModel.currentPage,
        pageSize: pageModel.pageSize,
      };
    };
    const setTotalSize = (totalSize: number) => {
      pageModel.totalSize = totalSize;
    };
    const setPageSize = (pageSize: number) => {
      pageModel.pageSize = pageSize;
    };
    const placement = computed(() => {
      switch (props.position) {
        case "left":
          return "justify-start";
        case "center":
          return "justify-center";
        case "right":
          return "justify-end";
        default:
          return "";
      }
    });
    const onRefresh = () => {
      emit("refresh");
    };
    return {
      pageModel,
      placement,
      pageSizeChanged,
      currentChanged,
      withPageInfoData,
      setTotalSize,
      setPageSize,
      onRefresh,
      RefreshIcon,
    };
  },
});
